/**
 * Gets the repositories of the user from Github
 */

import { call, put, takeLatest } from "redux-saga/effects";
import { saveTextFile } from "./actions";
import { postConfig } from "./config";

import { LOAD_FILE } from "./constants";
import { getSectionizedText } from "./utils";

/**
 * Github repos request/response handler
 */
export function* readTextFile(action: any) {
  try {
    const { id } = action.payload;
    const file = postConfig[id];
    const { textFile, photoFiles } = file;
    //@ts-ignore
    const text = yield call(() => readFile(textFile));
    const serializedObject = getSectionizedText(text);
    yield put(
      saveTextFile({
        id,
        file: { formattedObj: serializedObject, photos: photoFiles },
      })
    );
  } catch (err) {}
}

async function readFile(file: any) {
  const result = await fetch(file)
    .then((response) => {
      return response.text();
    })
    .then((text) => {
      return text;
    })
    .catch((error) => {
      throw error;
    });
  return result;
}

/**
 * Root saga manages watcher lifecycle
 */
export default function* getPost() {
  // Watches for LOAD_REPOS actions and calls getRepos when one comes in.
  // By using `takeLatest` only the result of the latest API call is applied.
  // It returns task descriptor (just like fork) so we can continue execution
  // It will be cancelled automatically on component unmount
  yield takeLatest(LOAD_FILE, readTextFile);
}
